@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
h2 {
    font-family: 'Ubuntu', sans-serif;
}

input {
    box-sizing: border-box;
    margin: 10px 0;
    padding: 10px;
    width: 60%;
    border: none;
    font-family: 'Ubuntu', sans-serif;
}

input[type=text]:focus {
    outline: none !important;
    background-color: #ffd969;
}

input[type=submit]:focus {
    outline: none !important;
    background-color: #34dbf4;
}

.optional:focus {
    outline: none;
    background-color: #364966 !important;
}
.App-header {
    height: 20vh;
    background-color: #010a01;
    text-align: center;
    font-family: 'Ubuntu', sans-serif;
    font-size: 95px;
    color: #fff;
}

.Content {
    height: 80vh;
    background-color: #010a01;
    display: flex;
    justify-content: center;
}

.Container {
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    width: 50%;
    top: 25%;
    padding-bottom: 3%;
    background-color: #E43F5A;
    position: absolute;
    z-index: 9999;
    border-radius: 15px;
    text-align: center;
}
.App-header {
    text-shadow:
      0 0 7px #fff,
      0 0 10px #fff,
      0 0 21px #fff,
      0 0 42px #0fa,
      0 0 82px #0fa,
      0 0 92px #0fa,
      0 0 102px #0fa,
      0 0 151px #0fa;
}

.first {
    animation: flicker 1.5s infinite alternate;
}

@keyframes flicker {
    0%, 62.999%, 64%, 64.999%, 72%, 100%  {
        text-shadow:
        0 0 4px #fff,
        0 0 11px #fff,
        0 0 19px #fff,
        0 0 40px #0fa,
        0 0 80px #0fa,
        0 0 90px #0fa,
        0 0 100px #0fa,
        0 0 150px #0fa;
    }
    63%, 63.999%, 65%, 71.999% {
        text-shadow: none;
        opacity: 0.1;
    }
}
